.nav{
    margin-top: 2em;
    margin-left: 55%;
    margin-right: 1em;
    margin-bottom: 1em;
    color:aqua;
    display:flex;
    gap:2em;
    font-size: large;
}
nav li{
    list-style: none;
}
span{
    color:rgb(175, 173, 170);
    margin-left:.4em;
}
span:hover{
    color:aqua;
}
nav li button{
    padding:.7em 1.5em .7em 1.5em;
    border: 1.5px solid aqua;
  background-color:#0a192f;
  color:aqua;
  border-radius: 5px;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: large;
  position: relative;
  bottom:.4em
}
.linkinnav{
    text-decoration: none;
    color: aqua;
}
.navbuttond{
    text-decoration: none;
    color:aqua;
}
@media only screen 
   and (max-width : 600px) {
    .nav{
        display:none;
    }
}
