.projecth1{
    text-align: center;
    color: aliceblue;
}
.projecth2{
text-align: center;
color:aqua;
font-size:1.5em;
}
.box{
    border:1px black;
    padding:2em 3em;
    background-color: rgb(29, 44, 80);
    border-radius: 5px;
}
.box:hover{
    transform:translateY(1em)
}
.box:hover h2{
color:aqua
}
.box a{
    color:whitesmoke;
}
.projecttheme{
    display:flex;
    gap:2em;
    justify-content: center;
    margin-top: 4em;
    height: 18em;
    margin-bottom: 3em;
}
.texteditorp{
    width:6cm ;
    color:rgb(208, 206, 204)
}
.inotebookp{
color:rgb(193, 197, 201);
}

@media only screen 
   and (max-width : 600px) {
    .projecth1{
        text-align: center;
        color: aliceblue;
        font-size: 1.5em;
        position: relative;
        top:2em;
        margin-top: 7em;
    }
    .projecth2{
    text-align: center;
    color:aqua;
    font-size:1em;
    position: relative;
    top:2.5em
    }
    .box{
        border:1px black;
        padding:2em 2em;
        background-color: rgb(29, 44, 80);
        border-radius: 5px;
        width: 6cm;
    }
    .box:hover{
        transform:translateY(1em)
    }
    .projecttheme{
        display:grid;
        gap:2em;
        row-gap: 2em;
        justify-content: center;
        margin-top: 4em;
        margin-bottom: 1em;
    }
    #boxp1b3 {
    visibility: hidden;
    }
    #boxp2b1{
        visibility: hidden;
    }
   }