.footer{
    text-align: center;
    margin-top: 7cm;
}
.footer4{
color:aqua;
font-size: 1.3em;
}
.footerh2{
font-size: 2.4em;
position: relative;
bottom:.8em;
color:beige;
}
.footerpa1{
    width: 50%;
    position: relative;
    left:7cm;
    bottom:2em;
    font-size: 1.5em;
}
.footerbl{
    padding: 1em 1.9em;
    border:2px solid aqua;
    border-radius: 5px;
    background-color:#0a192f;
    color:white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.foostersocial{
    visibility:hidden;
}
.footersocial a{
    visibility: hidden;
}
@media only screen 
   and (max-width : 600px) {
    .footer{
        text-align: center;
        margin-top: 15cm;
    }
    .footer4{
    color:aqua;
    font-size: 1em;
    }
    .footerh2{
    font-size: 2em;
    position: relative;
    bottom:.8em;
    color:beige;
    }
    .footerpa1{
        width: 90%;
        position:relative;
        left:2%;
        font-size: 1.5em;
    }
    .foostersocial{
        visibility: visible;
        margin-top: 1cm;
    }
    .fs{
        margin:1.1em;
        font-size: 1.5em;
    }
    .footerbl{
        position: relative;
        bottom:2em;
    }
   .footersocial a{
    visibility: visible;
        color:white;
    }
   }